header {
  .menu {
    .logo {
      max-width: 200px;

      a {
        padding: 0;
      }
    }
  }
}

nav {
  z-index: 99999;

  @include breakpoint(small only) {
    height: auto !important;

    &.sticky-container {
      position: fixed;
      top: 0;
      width: 100%;
    }
  }

  .dropdown {
    &.menu {
      @include breakpoint(small only) {
        flex-direction: column;
      }
    }
  }
}

.top-bar {
  transition: padding 0.25s ease;
  padding: 0;


  @include breakpoint(medium) {
    padding: 10px 0;

    .is-dropdown-submenu {
      transition: margin-top 0.25s ease;
      margin-top: 10px;
      border: 0;
    }

    &.is-stuck {
      padding: 5px 0;

      .is-dropdown-submenu {
        margin-top: 5px;
      }
    }
  }
}
