label {
    cursor: pointer;
}

form {
    &.max-width {
        max-width: 800px;
        margin: 0 auto;
    }

    &.inherit-style {
        padding: 0 10px;
        width: 100%;


        @include breakpoint(medium) {
            padding: 0 15px;
        }

        .field-wrapper {
            @include grid-row;

            span {
                display: inline-block;
            }

            &:not(:last-child) {
                margin-bottom: 20px;
            }

            &.checkbox-text {
                input[type="checkbox"] {
                    margin-left: -20px;
                    margin-bottom: 0;
                    vertical-align: top;
                }
            }

            .checkbox-wrapper {
                padding-left: 30px;

                input[type="checkbox"] {
                    margin-top: 5px;
                }
            }

            .checkbox-style {
                padding-left: 0;
                input[type="checkbox"] {
                    display: none;

                    &:checked + .custom-checkbox-wrap{
                        .custom-checkbox:before {
                            position: absolute;
                            top: 0;
                            left: 0;
                            right:0;
                            bottom:0;
                            margin: auto;
                            width: 12px;
                            height: 12px;
                            line-height: 1;
                            content: '✔';
                            background-position: center;
                            background-size: 70% auto;
                        }
                    }
                }

                .custom-checkbox {
                    position: relative;
                    display: inline-block;
                    width: 20px;
                    height: 20px;
                    float: left;
                    vertical-align: top;
                    border: 1px solid #cacaca;
                }

                .custom-checkbox-wrap {
                    margin-left: 0;
                    margin-right: 0;
                }

                .text-group {
                    padding-left: 30px;
                }
            }
        }

        .field-group.has-error {
            .custom-checkbox, input, select, textarea {
                border-color: #d90000;
            }

            .error-text {
                color: #d90000;
            }
        }

        input, select, textarea {
            margin: 0 0 0.5rem;
        }

        textarea {
            min-height: 100px;
        }

        .two-col-field {
            @include grid-row-nest;

            > div {
                @include grid-column(12);

                @include breakpoint(medium) {
                    @include grid-column(6);
                }

                @include breakpoint(small only) {
                    &:not(:last-child) {
                        margin-bottom: 20px;
                    }
                }
            }

            &.has-prefix {
                > div:first-child {
                    @include grid-column(3);
                }

                > div:last-child {
                    @include grid-column(9);
                }

                @include breakpoint(small only) {
                    div {
                        margin-bottom: 0;
                    }
                }
            }
        }

        input[type="submit"] {
            padding: 11px 20px;
            min-width: 150px;
            max-width: 100%;
            width: 100%;
            vertical-align: middle;
            margin-bottom: 0;

            @include breakpoint(medium) {
                width: auto;
            }

            + .spinner {
                display: none;
                width: auto;

                > img {
                    width: auto;
                }
            }

            &:disabled + .spinner {
                display: inline-block;
            }
        }

        .button-wrapper {
            text-align: right;
        }
    }
}
