
.wrapper-step{
    @include grid-column (12);
}
.howitsmade{
    background-color: #f8f6e7;
    .howitsmade-top{
        width: 100%;
        height: 100%;
        background-image: url(../images/howitsmade/wegiveafox.png);
        padding-top: 0;
        background-size: cover;
        // min-height: 314px;
        background-repeat: no-repeat;
        @include breakpoint(small only) {
            
            background-position: center;
            @include aspect-ratio(377,314);
        }
        @include breakpoint (medium){
         background-image: url(../images/howitsmade/wegiveafoxbig.png);
         padding-top: 100px;
         background-position-x: center; 
         @include aspect-ratio(1920,1400);
        //  background-size: contain;
        }
        // width: 100%;
        // height: 100%;
        // background-image: url(../images/howitsmade/wegiveafoxbig.png);
        // min-height: 1400px;
        // padding-top: 100px;
        // background-position-x: center; 
        // background-repeat: no-repeat;
        // background-size: contain;
        // @include breakpoint (medium){
        // //  background-image: url(../images/howitsmade/wegiveafoxbig.png);
        // //  min-height: 1400px;
        // //  padding-top: 100px;
        // //  background-position-x: center; 
        // // //  background-size: contain;
        // }
    }
    .step-section{
        background-color: #f8f6e7;
        padding-bottom: 100px;
        .wrapper-step{
            text-align: center;
            img{
                padding-top: 80px;
                width: 20%;
                @include breakpoint (medium){
                    width: auto;
                }
            }
            h3{
                font-family: Veneer, sans-serif;
                font-size: 2em;
                @include breakpoint (medium){
                    font-size: 3em;
                }
            }
            p{
                font-size: 0.7em;
                @include breakpoint (medium){
                    font-size: 1.8em;
                }
            }
        }
    }
    .ingredient-container{
        width: 100%;
        position: relative;
        padding: 50px 0;
        float: left;
        .columns{
            @media (max-width: 800px){
                padding-left: 0;
                padding-right: 0;
            }
        }
        .ingredient-bg{
            width: 100%;
             @media (max-width: 1279px){
                display: none;
             }
        }
        .content{
            @media (min-width: 1280px){
                position: absolute;
                z-index: 1;
                width: 100%;
                padding: 50px 0;
            }
        }
        .wtf-bottle{
            width: 100%;
            @media (min-width: 1280px){
                width: 46%;
                position: absolute;
                z-index: 1;
                top: -30%;
                left: 5%;
            }
            @media (min-width: 1500px){
                width: 45%;
            }
            &.bottle-desktop{
                @media (max-width: 1279px){
                    display: none;
                }
            }
            &.bottle-mobile{
                @media (min-width: 1280px){
                    display: none;
                }
            }
        }
        p{
            font-size: 1em;
            margin-top: 0;
        }
        .ingredient{
            float: left;
            width: 100%;
        }
        ul{
            margin: 0;
            @include breakpoint (medium){
                width: 40%;
                float: left;
                margin-right: 10%;
            }   

            li{
                list-style-type: none;
                display: inline-block;
                width: 78%;
                font-size: .8em;
                font-family: Verdana,Geneva,sans-serif;
                &:nth-child(odd){
                    font-weight: bold;
                }
                &:nth-child(even){
                    width: 20%;
                }
            }
        }
        .text-box{
            @media (min-width: 1280px){
                position: absolute;
                right: 0;
                float: right;
                width: 55%;
                margin-top: 0.5%;
                background-color: rgb(255,255,255);
                background-color: rgba(255,255,255,.6);
                padding: 20px 20px 20px 80px;
                border: 10px solid rgba(255,255,255,.8);
                border-right: none;
                -webkit-background-clip: padding-box; /* for Safari */
                background-clip: padding-box; /* for IE9+, Firefox 4+, Opera, Chrome */
            }
            @media (min-width: 1500px){
                margin-top: 3%;
                padding: 40px 40px 40px 100px;
            }
            @media (max-width: 800px){
                margin-top: 3%;
                padding: 20px;
            }
            .inner{
                max-width: 650px;
                @media (max-width: 1281px){
                    margin: 0 auto;
                }
            }

        }
    }
    .foximg{
        position: relative !important;
        margin: 0 auto;
        max-width: 600px;
        @media (max-width: 1279px){
            max-width: 300px;
        }
        h3 {
            font-family: Veneer,sans-serif;
        }
    }
    #foxmaskmarker{
        padding-top: 0;
        padding-bottom: 100px;
    }
    .apple_grass_footer{
        padding: 50px;
        h3 {
            font-family: Veneer,sans-serif;
        }
    }
}