#tncpage {
  font-size: 3em;
  letter-spacing: 0.1em;
  font-family: Verdana, Geneva, sans-serif;
  color: #000;
  background: #f7f5e3;
  padding-bottom: 100px;
  .appletermtitle{
    text-align: center;
  }
  .tnccontent {
    padding-top: 100px;
  }

  .listformat {
    font-weight: bold;
    font-size: 0.8em;
    letter-spacing: 0.05em;
    list-style: none;

    li {
      padding-left: 1.3em;
      margin-top: -15px;

      &:before {
        content: "c";
        /* FontAwesome Unicode */
        font-family: 'Wingdings 3';
        display: inline-block;
        margin-left: -1.3em;
        /* same as padding-left set on li */
        width: 1.3em;
        /* same as padding-left set on li */
        font-size: 14px;
      }
    }
  }

  .linktnc {
    color: #000 !important;
    font-size: 16px;
  }

  .tnctitle {
    font-family: veneerregular, sans-serif;
    font-size: 20px;
  }

  .tnctitlesmall {
    font-family: veneerregular, sans-serif;
    font-size: 16px;
    padding-top: 30px;
  }

  .tnctitletext {
