.off-canvas-content{
  background-color: #f8f6e7;
}
.wtfpage{
  background-image: url("../images/wtf/bg_dot.jpg");
  max-width: 1920px;
  margin: 0 auto;
  h1,  h2 {
    font-size: 4.2vw;
    line-height: 1em;
    font-family: 'veneerregular', sans-serif;;
    margin-bottom: 0;
    @include breakpoint (medium down){
      font-size: 16vw;
    }
  }
  p {
    font-size: 1.2vw;
    color: #231f20;
    line-height: 1.6vw;
    font-family: 'verdana', sans-serif;

    @include breakpoint (medium down){
      font-size: 4vw;
      line-height: 7vw;
    }
  }
  .row{
    @media (max-width: 1280px){
      max-width: 1000px;
    }
    @media (max-width: 480px){
      max-width: 95%;
    }
  }
  .landing {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 700px;
    background-image: url("../images/wtf/wtf_header.jpg");
    background-position: top center;
    background-repeat: no-repeat;
    @media (max-width: 1280px){
      min-height: 500px;
      background-size: cover;
    }
    @include breakpoint (medium down){
      background-image: url("../images/wtf/bg_dot.jpg");
      background-repeat: repeat;
      min-height: 300px
    }
    .landingContent {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0 auto;
      @media (max-width: 1280px){
        bottom: 10px;
      }
      @include breakpoint (medium down){
        bottom: auto;
        position: relative;
        padding-bottom: 30px;
      }
      img{
        @include breakpoint (medium down){
          width: 100%;
        }
      }
      p{
        text-align: center;
      }
    }
  }

  .whatthefoxinthebox-container {
    position: relative;
    width: 100%;
    height: 100%;
    color: white;
    min-height: 640px;
    background-image: url("../images/wtf/bg_wtf_inthebox.jpg");
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    @media (max-width: 1440px){
      min-height: 500px;
    }
    .secondContent {
      // position: absolute;
      // top: 0;
      // left: 0;
      // right: 0;
      // bottom: 0;
      // margin: auto;
      padding: 2% 0;
      @include breakpoint (medium down){
        width: 100%;
        position: relative;
        padding: 10% 0;
      }
      h2,p{
        color: white;
      }
    }
    .columns{
      position: relative;
    }
    .findthefox_box{
      max-width: 120%;
      width: 120%;
      position: absolute;
      bottom: -70px;
      @media (max-width: 1440px){
        bottom: -120px;
      }
      @include breakpoint (medium down){
        width: 100%;
        margin-top: 30px;
        position: relative;
        bottom: auto;
      }
    }
    .tncside{
      color: white;
      bottom: 70px;
      @media (max-width: 480px){
        bottom: 30px;
        color: #231f20;
      }
    }
  }

  .wantafreeapplefox-container {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #f8f6e7;
    .thirdContent {
      margin: 0 auto;
      padding: 4% 0;
      .thirdProduct {
        // text-align: right;
        @include breakpoint (medium down){
          text-align: center;
          margin-bottom: 50px;
        }

        .productImg {
          max-width: 100%;
          @include breakpoint (medium down){
            max-width: 200px;
            width: 100%;
          }
        }
      }

      .thirdDesc {
        padding: 6% 2%;

      }

    }
  }

  .foxwithfriend-container {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 632px;
    background-image: url('../images/wtf/bg_wtf_foxwithfriend.jpg');
    background-repeat: no-repeat;
    background-position: center top;
    @include breakpoint (medium down){
      background-image: url('../images/wtf/bg_wtf_foxwithfriend_mobile2.jpg');
      background-size: cover;
      background-position: right top;
    }
    .fourthContent {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      padding: 9% 0;
    }
  }

  .findthefox-container {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-color: #f8f6e7;
    padding-bottom: 2%;
    h2{
      margin-bottom: 50px;
    }
    .fifthContent {
      margin: 0 auto;
      padding: 4% 0;
      width: 90%;
      .filter {
        max-width: 100%;
        flex-flow: nowrap;

        @include breakpoint (small down) {
          flex-direction: column;
        }

        .searchHolder {
          border: 2px solid #000;
          padding: 0;
          margin: 0;
          display: flex;
          align-items: center;
          justify-content: space-between;
          min-height: 45px;

          @include breakpoint (medium down) {
            margin: 4px 0;
          }

          .search {
            margin: 0;
            padding: 0 16px;
            background-color: transparent;
            border: none;
            box-shadow: none;
            font-size: 1.2vw;

            @include breakpoint (medium down) {
              padding: 0 10px;
            }

            @include breakpoint (small down) {
              font-size: 4vw;
              padding: 0 16px;
            }
          }

          .searchIcon {
            padding: 3%;
            cursor: pointer;
            width: 15%;

            @include breakpoint (medium down) {
              padding: 7px;
              width: 20%;
            }

            @include breakpoint (small down) {
              padding: 2%;
              width: 10%;
            }
          }
        }

        .dropDown {
          padding: 0;
          margin-left: 10px;
          min-height: 45px;

          &:first-child{
            margin-left: 0;
          }

          @include breakpoint (medium down) {
            margin: 4px 0 4px 10px;
          }

          @include breakpoint (small down) {
            margin: 4px 0;
          }

          select {
            margin: 0;
            background-color: #000;
            color: #fff;
            height: auto;
            padding-left: 16px;
            font-size: 1.2vw;
            min-height: 45px;
            border: 1px solid #000;

            @include breakpoint (small down) {
              font-size: 4vw;
            }
          }
        }

        .selectDate {
          border: 2px solid #000;
          padding: 0;
          margin: 0 0 0 10px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          min-height: 45px;
          color: #fff;
          background-color: #000;

          input {
            background-color: #000;
            margin: 0;
            border: none;
            box-shadow: none;
            font-size: 1.2vw;
            color: #fff;

            @include breakpoint (medium down) {
              padding: 0 10px;
            }

            @include breakpoint (small down) {
              font-size: 4vw;
              padding: 0 16px;
            }
          }

          ::-webkit-input-placeholder {
            color: #fff;
          }

          @include breakpoint (medium down) {
            margin: 4px 0 4px 10px;
          }

          @include breakpoint (small down) {
            margin: 4px 0;
          }
        }
      }
    }

    .appendAddress {
      max-width: 90%;
      .address {
        margin-bottom: 30px;
        line-height: 1.4;

        .addressTitle {
          font-size: 1.4vw;
          font-weight: bold;
          text-transform: uppercase;
          margin: 0;

          @include breakpoint (medium only){
            font-size: 2vw;
            line-height: 4vw;
          }

          @include breakpoint (small down){
            font-size: 5vw;
            line-height: 6vw;
          }
        }

        p{
          margin: 0;
          font-size: 1vw;
          line-height: 1.4vw;

          @include breakpoint (small down){
            font-size: 3vw;
            line-height: 3.5vw;
          }
        }
        //
        // .addressDesc {
        //   font-size: 1.2vw;
        //   margin: 0;
        //
        //   @include breakpoint (medium only){
        //     font-size: 2vw;
        //     line-height: 2.5vw;
        //   }
        //
        //   @include breakpoint (small down){
        //     font-size: 5vw;
        //     line-height: 6vw;
        //   }
        // }
      }
    }
  }
  .tncside{
    font-size: 0.7vw;
    position: absolute;
    right: 10px;
    bottom: 0;
    @include breakpoint (small down){
      font-size: 2vw;
      margin-top: 50px;
    }
  }
  .backto{
    float: right;
    font-size: 2vw;
    line-height: 1em;
    color: black;
    font-family: 'veneerregular';
    bottom: 10%;
    right: 5%;
    position: fixed;
    opacity: 0;
    @include breakpoint (small down){
      font-size: 6vw;
      right: 10px;
    }
    &.show{
      opacity: 1;
    }
  }
  .finthefoxcta{
      @include breakpoint (small down){
        width: 120px;
        margin-top: 20px;
        position: relative;
        display: inline-block;
      }
  }
  .footer{
    padding-bottom: 50px;
    .apple_grass_footer{
      margin-left: 5%;
      @include breakpoint (small down){
        text-align:center;
        a{
          display: inline-block;
        }
      }
      a{
        padding: 10px;
      }
    }

    h3{
      font-family: 'veneerregular', sans-serif;;
      @include breakpoint (small down){
        font-size: 0.7em;
      }
      sup{
        font-size: 0.5em;
        top: -1em;

        @include breakpoint (small down){
          top: -.5em;
        }
      }
    }
  }
}
