@charset 'utf-8';
@import 'settings';
@import 'foundation';
@import 'motion-ui';
@include foundation-global-styles;

/*
**GRID SETTINGS**
Types of Grid Layouts
(!IMPORTANT: Please assign the value of any selected layout type to the $grid-type variable.)
1. float
2. flex
3. xy
*/

$grid-type: 'flex';

@if $grid-type == 'flex' {
    $global-flexbox: true;
    @include foundation-flex-classes;
    @include foundation-flex-grid;
}
@else if $grid-type == 'xy' {
    @include foundation-xy-grid-classes;
}
@else {
    @include foundation-grid;
}

