.homepage{
     font-family: Verdana, Geneva, sans-serif;
     color: #000;
     background: url(../images/homepage/bg_dot.jpg);
     max-width: 100%;
     overflow-x: hidden;
     @include breakpoint (medium down){
       background: url(../images/homepage/bg_dot2.jpg);
     }
     p{
         font-size: 1.8em;
         letter-spacing: 0.1em;
          @include breakpoint (small down){
            font-size: 1em;
          }
    }
    .hometop-text{
      width: 100%;
      height: 100%;
      background-image: url(../images/homepage/applefoxcider1bg_mobile.jpg);
      padding-top: 0;
      min-height: 300px;
      background-repeat: no-repeat;
      @include breakpoint (medium){
       background-image: url(../images/homepage/applefoxcider1bg.jpg);
       min-height: 1400px;
       padding-top: 100px;
       background-position-x: center; 
      }
      .wrapper{
        @include grid-column (6);
        @include breakpoint (medium down){
          @include grid-column (12);
        }
        
        &.applecider{
          img{
            padding-top: 90px;
          }
        }
        &.findapplefox{
          text-align: center;
        }
        &.single-item{
          .slick-slide{
            img{
              margin: 0 auto;
            }
          }
          .slick-arrow{
            &.slick-prev{
              left: -10px;
              // @include breakpoint (small down){
              //   left: 0;
              //   top: 450px;
              // }
              &:before{
                content: "<";
                font-family: 'veneerregular';
                color: white;
                font-size: 80px;
                opacity: 1;
                 @include breakpoint (small only){
                   color: black;
                   font-size: 60px;
                   padding-left: 10px;
                 }
              }
          }
          &.slick-next{
              background-position: bottom;
              // @include breakpoint (small down){
              //   right: 0;
              //   top: 450px;
              // }
              &:before{
                content: ">";
                font-family: 'veneerregular';
                color: white;
                font-size: 80px;
                opacity: 1;
                @include breakpoint (small down){
                  color: black;
                  font-size: 60px;
                }
             }
           }
          }
          .slick-disabled{
            opacity: 0 !important;
          }
        }
      }
    }
     .apple_tree {
         position: absolute;
         top: 0;
         left: 0;
    }
     .apple_grass {
         position: absolute;
         bottom: 0;
         right: 0;
    }
     .mb15 {
         margin-bottom: 15px;
    }
     .contentCell {
         display: table-cell;
         vertical-align: middle;
    }
     .contentWrapper {
         width: 100%;
         display: table;
         padding: 20px 0 0 20px;
    }
     .inspiredbynewzealand-container {
         padding-top: 100px;
        //  padding-bottom: 100px;
         background: url(../images/homepage/bg_dot2.jpg);
         .mobile-test{
           width: 100%;
         }
         background-repeat: no-repeat;
         .text{
           background: url(../images/homepage/bg_dot2.jpg);
           padding-bottom: 100px;
         }
    }
     .bottom-container {
         background: url(../images/homepage/bg_dot3.jpg);
         position: relative;
         height: auto;
         min-height: 1100px;
         overflow-y: hidden;
         @include breakpoint (small down){
           height: auto;
           min-height: 100px;
         }
         .apple_tree {
             position: absolute;
             top: 0;
             left: 0;
             @include breakpoint (small down){
               position: relative;
             }
         }

         .apple_grass_footer {
           margin-bottom: 5%;
           margin-left: 5%;
           h3{
             font-family: Veneer, sans-serif;
           }
         }
         .foxmask {
            padding-top: 500px;
             @include breakpoint (small down){
               padding-top: 0;
               margin-top: -100px;
             }
        }
         .foximg{
             position: absolute;
             z-index: 1;
             text-align: center;
             width: 100%;
              @include breakpoint (small down){
                position: relative;
              }
             img{
                 width: 100%;
                 max-width: 640px;
            }
             h3 {
                 font-family: Veneer, sans-serif;
            }
        }
        .apple_grass_footer{
          position: absolute;
          bottom: 0;
          @include breakpoint (small down){
            position: relative;
            width: 150px;
          }
          h3{
            @include breakpoint (small down){
              font-size: 0.7em;
            }
          }
        }
    }
  }