h1,
p {
	margin: 1vw 0;
  // font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-family: Verdana, Geneva, sans-serif;
}
h1 {
	font-size: 10vw;
  line-height: 1;
  color: $primary-color;
}
// img {
// 	width: 100%;
