label, a, button, .btn, .button {
  cursor: pointer;
}

section {
  > .introduction {
    margin-bottom: 20px;
  }

  &.set-gutter {
    padding-top: 15px;
    padding-bottom: 15px;

    @include breakpoint(medium) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
}

