//applefox page style//

.mobile-test {
  display: block;

  @include breakpoint (medium) {
    display: none;
  }
}

.desktop-test {
  display: none;

  @include breakpoint (medium) {
    display: block;
    margin: 0 auto;
  }
}

.desktop {
  display: block;

  @include breakpoint (medium down) {
    display: none;
  }
}

.mobile {
  display: none;

  @include breakpoint (medium down) {
    display: block;
  }
}

@font-face {
  font-family: 'veneerregular';
  src: url('../fonts/veneer-webfont.woff2') format('woff2'),
       url('../fonts/veneer-webfont.woff') format('woff'),
       url('../fonts/veneer-webfont.ttf') format('truetype'),
       url('../fonts/veneer-webfont.svg#veneerregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
